.cart-items {
  list-style: none;
  max-height: 20rem;
  overflow-y: scroll;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 1rem 0;
}

.submit-order p {
  text-align: center;
}

.actions {
  text-align: right;
}

.actions button {
  font: inherit;
  cursor: pointer;
  background-color: transparent;
  border: 1px solid #8a2b06;
  padding: 0.5rem 2rem;
  border-radius: 2.5rem;
  margin-left: 1rem;
  transition: 0.3s;
}

.actions button:hover,
.actions button:active {
  background-color: #5a1a01;
  border-color: #5a1a01;
  color: #fff;
}

.actions .button--close {
  color: #8a2b06;
}

.actions .button--order {
  background-color: #8a2b06;
  color: #fff;
}
